body{
  text-align: justify;
}
footer {
  width: 100%;
  background-color: #333;
  color:#fff;
  padding: 10px;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}
.main-modal {
  position:fixed;
  background: white;
  width: 80%;
  height: auto;
  top:50%;
  left:50%;
  padding: 10px;
  transform: translate(-50%,-50%);
}
.display-block {
  display: block;
}
.display-none {
  display: none;
}

.introdiv, .navbar{
  padding: 10px 150px 10px 150px;
}
#border1{
  /* border-bottom: 5px solid red; */
  border-bottom: 8px solid #FE0000 !important;
  border-radius: 10px !important;
}
.cropenings{
  margin: 50px 0px 50px 0px;
}
.careershead{
  margin: 30px 0px 30px 0px;
}
.border2{
  min-width: 33%;
  /* margin: 10px; */
  margin:10px 20px 20px 10px !important;
}
.footer1{
  background-color: #E8E8E8;
  padding: 50px 150px 40px 150px;
  margin:30px 0px 0px 0px;
}
.facebookimg, .youtubeimg,.linkedinimg{
